import Image from 'next/image'
import Logo from './ui/logo.png'
import { Button } from './ui/button'
import Link from 'next/link'

export default function AboutSection() {
    return (
        <section className="py-16 md:py-32">
            <div className="mx-auto max-w-5xl space-y-8 px-6 md:space-y-16">
                <h2 className="relative z-10 max-w-xl text-4xl font-medium lg:text-5xl">
                    The Lyra ecosystem brings together our models.
                </h2>
                <div className="grid gap-6 sm:grid-cols-2 md:gap-12 lg:gap-24 items-center">
                    <div className="relative flex flex-col justify-center space-y-4">
                        <p className="text-muted-foreground">
                            Gemini is evolving to be more than just the models.{' '}
                            <span className="text-accent-foreground font-bold">
                                It supports an entire ecosystem
                            </span>{' '}
                            â€” from products innovate.
                        </p>
                        <Button
                        size="lg"
                        asChild>
                        <Link href="#">
                            <span className="text-nowrap">Lorem Lorem</span>
                        </Link>
                        </Button>
                    </div>

                    <div className="relative mt-6 sm:mt-0">
                        <div className="relative rounded-2xl p-px">
                            <Image src={Logo} alt="Logo" />
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
